<template>
  <div class="container">
    <div class="info">
       Github  URL <b
        > MD  ~</b
      >

      <div>
        
        <span>
          https://github.com/azl397985856/leetcode/blob/master/thinkings/binary-search-1.md
        </span>
        
        <span>
          https://github.com/azl397985856/leetcode
        </span>
      </div>
    </div>
    <a-input
      style="width: auto; flex: 1;"
      :value="keyword"
      @input="handleInputChange"
      type="text"
      placeholder=" Markdown  Github "
    />

    <a-button
      :disabled="!dest"
      type="link"
      :href="`/solutionDetail?type=1&url=${dest}`"
      target="_blank"
      ></a-button
    >

    <div style="margin: 10px 0"> URL   markdown </div>

    <a-input
      style="width: 800px; flex: 1;"
      :value="raw"
      @input="handleRawInputChange"
      type="textarea"
      rows="10"
      placeholder=" Markdown "
    />
    <a-button
      :disabled="!raw"
      type="link"
      @click="goToRenderRaw"
      target="_blank"
      ></a-button
    >

    <div class="footer">
      ~
    </div>
  </div>
</template>

<script>
import { setStorage } from '../utils/storage'
const getURL = ({ owner, repo, path }) =>
  `https://api.github.com/repos/${owner}/${repo}/contents/${path}`
function getEXT(filename) {
  if (!filename) return ''
  return filename
    .split('.')
    .pop()
    .toLowerCase()
}
function getOwnerAndRepo(url) {
  if (!url) return {}
  const parts = url.split('/')
  if (!parts || parts[2] !== 'github.com')
    throw new Error('NO_GITHUB', ' github ')
  if (!parts[3] || !parts[4])
    throw new Error(
      'URL_INVALID',
      'https://github.com/azl397985856/leetcode/blob/master/thinkings/binary-search-1.md'
    )
  return {
    owner: parts[3],
    repo: parts[4],
    path: parts[7] ? parts.slice(7).join('/') : '/README.md'
  }
}
export default {
  data() {
    return {
      keyword: '',
      raw: ''
    }
  },
  methods: {
    async goToRenderRaw() {
      await setStorage('github-reader', this.raw)
      this.$router.push('/solutionDetail?type=5')
    },
    handleInputChange(e) {
      this.keyword = e.target.value
    },
    handleRawInputChange(e) {
      this.raw = e.target.value
    }
  },
  computed: {
    dest() {
      const { owner, repo, path } = getOwnerAndRepo(this.keyword)
      if (getEXT(path) !== 'md') return ''
      return getURL({ owner, repo, path })
    }
  }
}
</script>

<style lang="less" scoped>
.info {
  margin: 0 0 50px 0;
}
.footer {
  margin: 50px 0;
}
</style>
